// #######################################################################
function getRandomData(length, min, max, multiplier = 10, maxDiff = 10) {
  var array = new Array(length).fill();
  let lastValue;

  return array.map((item, index) => {
    let randomValue = Math.floor(Math.random() * multiplier + 1);

    while (
      randomValue <= min ||
      randomValue >= max ||
      (lastValue && randomValue - lastValue > maxDiff)
    ) {
      randomValue = Math.floor(Math.random() * multiplier + 1);
    }

    lastValue = randomValue;

    return { value: randomValue };
  });
}

function getRealData(category) {
  if (category === "linux")
  {
    return [ 
      {value: 1000}, 
      {value: 3000}, 
      {value: 2000}, 
      {value: 4000}, 
      {value: 7000}, 
      {value: 4000}, 
      {value: 2000}, 
      {value: 2000},
      {value: 1000}, 
      {value: 3000}, 
      {value: 2000}, 
      {value: 4000}, 
      {value: 2000}, 
      {value: 4000}, 
      {value: 2000}, 
      {value: 2000},
    ];
  }
  else if(category === "windows") {
    return [ 
      {value: 4000},
      {value: 3000}, 
      {value: 2000}, 
      {value: 1000}, 
      {value: 3000}, 
      {value: 1000}, 
      {value: 4000}, 
      {value: 5000}, 
      {value: 1000},
      {value: 3000}, 
      {value: 9000}, 
      {value: 4000}, 
      {value: 5000}, 
      {value: 1000},
      {value: 4000}, 
      {value: 5000},
    ];
  }
}

function getMainChartData() {
  var resultArray = [];
  // var linux = getRandomData(31, 3500, 6500, 7500, 1000);
  // // var desktop = getRandomData(31, 1500, 7500, 7500, 1500);
  // var windows = getRandomData(31, 1500, 7500, 7500, 1500);
  var linux = getRealData("linux");
  var windows = getRealData("windows");

  for (let i = 0; i < linux.length; i++) {
    resultArray.push({
      linux: linux[i].value,
      // desktop: desktop[i].value,
      windows: windows[i].value,
    });
  }

  return resultArray;
}
