import React, { useState, useEffect } from "react";
import { Grid, CircularProgress } from "@material-ui/core";
import MUIDataTable from "mui-datatables";

// components
import PageTitle from "../../components/PageTitle";
// import Widget from "../../components/Widget";
// import Table from "../dashboard/components/Table/Table";
import ProductEdit from "./productEdit";

// // data
// import mock from "../dashboard/mock";

import axios from "axios";
import config from "../../config/config.json";

//import { useAdminUserState, useAdminUserDispatch, getUserList } from "../../context/AdminUserContext";

const columns = [
  {
   name: "_id",
   label: "Id",
   options: {
    filter: true,
    sort: true,
   }
  },
  {
   name: "name",
   label: "Name",
   options: {
    filter: true,
    sort: false,
   }
  },
  {
   name: "price",
   label: "Price",
   options: {
    filter: true,
    sort: false,
   }
  },
  {
   name: "rating",
   label: "Rating",
   options: {
    filter: true,
    sort: false,
   }
  },
  {
    name: "createdAt",
    label: "CreatedAt",
    options: {
     filter: true,
     sort: false,
    }
   },
   {
    name: "updatedAt",
    label: "UpdatedAt",
    options: {
     filter: true,
     sort: false,
    }
   },
  //  {
  //   name: "__v",
  //   label: "__v",
  //   options: {
  //    filter: true,
  //    sort: false,
  //    display: false,
  //   }
  //  },
 ];
 
function ProductList() {

  var [isLoading, setIsLoading] = useState(false);
  var [error, setError] = useState(null);

  var [productlist, setProductList] = useState(null);

  var [showedit, setShowEdit] = useState(false);
  var [selected, setSelected] = useState(null);

  useEffect(() => {
    axios.get(config.ALL_PRODUCTS)
    .then(response => {
        if (response.data.length > 0) {
          setProductList(response.data);
        }
    })
    .catch((error) => {
    })

  }, []);

  return (
    <>
      <PageTitle title="Products" />
      <Grid container spacing={4}>
        <Grid item xs={12}>

          { productlist ? <MUIDataTable
            title="Product List"
            data={productlist}
            columns={columns}
            options={{
              filterType: "checkbox",

              onRowsDelete: ( selectedData ) => {
                let productsArray = [];
                selectedData.data.map ( item => {
                  productsArray.push(productlist[item.index]['_id']);
                });
                axios.post(config.DELETE_PRODUCT, productsArray)
                .then(res => {
                })
                .catch(function (error) {
                });
              },

              onRowClick: (rowData, rowMeta) => {
              },

              onRowsSelect: (currentRowsSelected, allRowsSelected) => { 
                setShowEdit(true); 
                setSelected(productlist[currentRowsSelected[0].index]);
              },
            }}
          /> : <CircularProgress /> }
        </Grid>
      </Grid>

      {showedit ? <ProductEdit data={selected} /> : ""}
    </>
  );
}

export default ProductList;