import React, { useState } from "react";
import {
  Grid,
  CircularProgress,
  Typography,
  Button,
  Tabs,
  Tab,
  TextField,
  Fade,
} from "@material-ui/core";

// styles
import useStyles from "../../components/Layout/styles";

// components
import PageTitle from "../../components/PageTitle/PageTitle";

import axios from "axios";
import config from "../../config/config.json";

import { DropzoneArea } from 'material-ui-dropzone'

function ProductAdd(props) {
  var classes = useStyles();

  // local
  var [isLoading, setIsLoading] = useState(false);
  var [error, setError] = useState(null);

  var [nameValue, setNameValue] = useState("");
  var [priceValue, setPriceValue] = useState("");
  var [ratingValue, setRatingValue] = useState(0);

  var [uploadFiles, setUploadFiles] = useState([]);

    const addProduct = () => {
        setTimeout(() => {
            const data = new FormData();
            data.append('file', uploadFiles);

            const Product = {
                name: nameValue,
                price: priceValue,
                rating: ratingValue,
            }
            var str = JSON.stringify(Product);
            data.append('product', str);

            axios.post(config.ADD_PRODUCT, data)
            .then(res => {
                setNameValue("");
                setPriceValue("");
                setRatingValue(0);
                setUploadFiles("");
            })
            .catch(function (error) {
            });
        }, 1000);
    }

    const fileUpload = (files) => {
        setUploadFiles(files[0]);
    }

  return (
    <>
    <PageTitle title="Add Product" />
    <Grid container className={classes.container}>
        <Fade in={error}>
        <Typography color="secondary" className={classes.errorMessage}>
            Something is wrong with your login or password :(
        </Typography>
        </Fade>
        <TextField
        id="name"
        InputProps={{
            classes: {
            underline: classes.textFieldUnderline,
            input: classes.textField,
            },
        }}
        value={nameValue}
        onChange={e => setNameValue(e.target.value)}
        margin="normal"
        placeholder="Name"
        type="text"
        fullWidth
        />
        <TextField
        id="price"
        InputProps={{
            classes: {
            underline: classes.textFieldUnderline,
            input: classes.textField,
            },
        }}
        value={priceValue}
        onChange={e => setPriceValue(e.target.value)}
        margin="normal"
        placeholder="Price"
        type="text"
        fullWidth
        />
        <TextField
        id="rating"
        InputProps={{
            classes: {
            underline: classes.textFieldUnderline,
            input: classes.textField,
            },
        }}
        value={ratingValue}
        onChange={e => setRatingValue(e.target.value)}
        margin="normal"
        placeholder="Rating"
        type="text"
        fullWidth
        />

        <DropzoneArea 
            onChange={e => fileUpload(e)}
            filesLimit={1}
            dropzoneClass="MuiGrid-grid-xs-12"
        />

        <div className={classes.creatingButtonContainer}>
        {isLoading ? (
            <CircularProgress size={26} />
        ) : (
            <Button
            onClick={() =>
                addProduct()
            }
            disabled={
                priceValue.length === 0 ||
                ratingValue.length === 0 ||
                nameValue.length === 0
            }
            size="large"
            variant="contained"
            color="primary"
            fullWidth
            className={classes.createAccountButton}
            >
            Add your product
            </Button>
        )}
        </div>
    </Grid>
    </>
  );
}

export default ProductAdd;
